<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 作业项目
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-button class="fr" type="primary" @click="openLink" size="small">新建作业项目</el-button>
                </div>
                <div class="cont">
                    <div class="cont-one" v-for="item in items">
                        <div class="one-left">
                            <h2>作业项目一</h2>
                            <p class="stu-num"><span>创建者：李明明</span> <span style="margin-left:20px;">创建时间：2019-10-15 12:00</span></p>
                            <p class="work-time">简介：阿嘎国家理科高建安路价格拉开关商量个家里开始价格两三个睡觉了攻击力关键是两个垃圾赶快拉几个拉几个垃圾啊考虑过价来看哥吉拉开个家里宫颈癌离开过阿嘎国家理科高建安路价格拉开关商量个家里开始价格两三个睡觉了攻击力关键是两个垃圾赶快拉几个拉几个垃圾啊考虑过价来看哥吉拉开个家里宫颈癌离开过</p>
                            <p class="data">资料：<span>上来讲是客观.png</span></p>
                            <p class="status">使用：<span>13次</span></p>
                        </div>
                        <div class="one-right">
                            <p><el-button type="danger" size="mini" @click="delworks" plain>删除作业</el-button></p>
                            <p><el-button type="primary" size="mini" @click="openLink" plain>编辑作业</el-button></p>
                        </div>
                    </div>

                    <div class="pagination">
                        <el-pagination
                          background
                          layout="prev, pager, next"
                          :total="1000">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            choosed:'未完成课程设计教学',
            items:[1,1,1]
        };
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        delworks(){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        }
    }
};
</script>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .cont-one{
        display:flex;
        padding:15px 20px;
        border:1px solid #ccc;
        margin-top:20px;
        .one-left{
            flex:1;
            font-size:14px;
            min-width:0;
            line-height:24px;
            h2{
                font-size:16px;
                line-height:28px;
            }
            .work-time,.data{
                overflow: hidden;
                width:100%;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
        }
        .one-right{
            width:90px;
            padding-top:20px;
            p{
                margin-top:10px;
            }
            button{
                text-align: right;
                display:block;
                margin-left:10px;
            }
        }
    }
    .pagination{
        margin-top:20px;
    }
</style>


